}
.team-grid {
  position: relative;
  clear: both;
  margin: 0 auto;
  padding: 1em 0 4em;
  max-width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 30px;
  list-style: none;
  text-align: center;
}
.team-card {
  position: relative;
  border: 1px solid #fbd46d;
  padding: 20px;
  border-radius: 150px;
}
.team-card::before {
  position: absolute;
  content: "";
  left: 0px;
  top: 0px;
  width: 0px;
  height: 100%;
  border-radius: 150px;
  box-shadow: inset 0 0 25px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
  background: #fbd46d;
  /* background-image: linear-gradient(to right, #3c70a4 0%, #64b2cd 100%); */
}
.team-card:hover::before {
  width: 100%;
}
.team-card img {
  position: relative;
  border-radius: 50%;
  width: 200px;
  height: 200px;
}
.team-card .team-des {
  position: relative;
  padding: 10px 0 30px 0;
}
.team-card .team-des h5 {
  color: #fbd46d;
  font-size: 24px;
  padding: 5px 0;
}
.team-card .team-des p {
  color: #fbd46d;
  font-size: 16px;
}
.team-card:hover .team-des h5 {
  color: #07031a;
}
.team-card:hover .team-des p {
  color: #07031a;
}
.team-card .team-social {
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding-bottom: 30px;
}
.team-card .team-social li a {
  position: relative;
  width: 50px;
  height: 50px;
  display: inline-block;
  margin: 0 5px;
  border-radius: 50%;
  border: 1px solid #4f8a8b;
}
.team-card:hover .team-social li a {
  border: 1px solid #07031a;
}
.team-card .team-social li a img {
  position: relative;
  border-radius: 0%;
  width: 45px;
  height: 45px;
  padding: 10px;
  display: block;
  margin: auto auto;
}
.team-card:hover .team-social li a img {
  filter: invert(1);
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .team-grid {
    max-width: 90%;
  }
}

@media screen and (max-width: 768px) {
  .team-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 0.6fr));
  }
  .team-card {
    margin: auto;
  }
  .team h1 {
    font-size: 28px;
  }
  .team h1::after {
    width: 100px;
  }
}
.about {
  position: relative;
  display: flex;
  width: 100%;
  padding: 50px 0;
  background: #f4f6ff;
}

.aboutwrap {
  position: relative;
  width: 100%;
  max-width: 1020px;
  padding: 40px 20px;
  margin: auto;
}
.aboutwrap h1 {
  display: flex;
  flex-direction: column;
  color: #07031a;
  text-align: center;
  font-size: 50px;
}
.aboutwrap h1::after {
  position: relative;
  content: "";
  width: 100px;
  height: 2px;
  margin: auto;
  background: #ea830e;
}
.aboutwrap p {
  color: #4f8a8b;
  text-align: center;
  font-size: 24px;
  padding: 30px 0;
}
.aboutwrap p span {
  font-weight: 600;
  color: #ea830e;
}
.contact {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 100px auto;
  justify-content: space-between;
  align-items: center;
}
.contact img {
  margin: auto;
  max-width: 400px;
}

.card {
  margin: auto;
  color: #fff;
  width: 600px;
  border-radius: 3px;
  text-align: center;
  overflow: hidden;
}

.form {
  padding: 2rem;
}

.button {
  display: block;
  width: 100%;
  background-color: #fbd46d;
  border: none;
  border-radius: 3px;
  padding: 1ch;
  font-size: 1rem;
  color: #07031a;
  font-family: inherit;
}
.text-input {
  position: relative;
  margin-bottom: 1rem;
}
.text-input label {
  position: absolute;
  top: 1ch;
  left: 1ch;
  padding: 0 0.5ch;
  pointer-events: none;
  transition: 150ms ease;
  color: #888;
}
.text-input label.label-focus {
  position: absolute;
  top: 0;
  font-size: 0.9em;
  color: #07031a;
  font-weight: bold;
  transform: translateY(-50%);
  background-color: #fbd46d;
  font-weight: 600;
}
.text-input input.input-focus {
  border-color: #fbd46d;
}
.text-input input {
  display: block;
  width: 100%;
  border: 2px solid #eee;
  padding: 1ch;
  border-radius: 3px;
  transition: 150ms ease;
  outline: none;
  font-size: 1rem;
  font-family: inherit;
}

.text-area {
  position: relative;
  margin-bottom: 1rem;
}
.text-area label {
  position: absolute;
  top: 1ch;
  left: 1ch;
  padding: 0 0.5ch;
  pointer-events: none;
  transition: 150ms ease;
  color: #888;
}
.text-area label.label-focus {
  position: absolute;
  top: 0;
  font-size: 0.9em;
  color: #07031a;
  font-weight: bold;
  transform: translateY(-50%);
  background-color: #fbd46d;
  font-weight: 600;
}
.text-area textarea.input-focus {
  border-color: #fbd46d;
}
.text-area textarea {
  display: block;
  width: 100%;
  border: 2px solid #eee;
  padding: 1ch;
  border-radius: 3px;
  transition: 150ms ease;
  outline: none;
  font-size: 1rem;
  font-family: inherit;
  height: 8em;
}
@media screen and (max-width: 768px) {
  .contact {
    margin: 50px auto;
  }
  .contact img {
    margin: 20px auto;
    max-width: 300px;
  }
}
